import eventDetailCard from "@/components/eventDetailCard";
import bookEventForm from "@/components/bookEventForm";
import BookEventList from "@/components/bookEventList";
import meSideMenu from "@/components/meSideMenu";
import { EVENTS_MY_EVENT, EVENTS_CATEGORY, EVENT_APPLICATIONS, EVENTS_ADD_EVENT } from '@/api/api';
import { encode } from 'js-base64';
export default {
  name: "events",
  components: {
    meSideMenu,
    eventDetailCard,
    bookEventForm,
    BookEventList
  },

  data() {
    return {
      userInfo: {},
      basicUserInfo: {},
      eventsList: [],
      eventPage: 1,
      eventLimit: 10,
      eventTotalNum: 0,
      options: [],
      categoryId: '',
      showVendorCompanyData: [],
      categoryOptions: [],
      eventDialogVisible: false,
      eventDetailData: {},
      bookEventDialogVisible: false,
      bookListDialogVisible: false,
      bookedListData: []
    };
  },

  mounted() {
    this.getEventCategories();
    this.getMyEvents(this.eventPage, this.eventLimit);
  },

  methods: {
    showBookEvent() {
      this.bookEventDialogVisible = true; // this.eventDialogVisible = false;
    },

    deleteEvent(eventId) {
      let params = {
        event_id: eventId,
        is_delete: 1
      };
      EVENTS_ADD_EVENT(params).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);
      });
    },

    editEvent(item) {
      let s = encode(JSON.stringify(item));
      this.$router.push({
        path: '/events/post',
        query: {
          str: s
        }
      });
    },

    showBookList(item) {
      this.bookListDialogVisible = true;
      let params = {
        event_id: item.id
      };
      EVENT_APPLICATIONS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.bookedListData = res.message.data;
        }
      }).catch(err => {
        console.log(err);
      });
    },

    showEventDialog(item) {
      this.eventDetailData = item;
      this.eventDialogVisible = true;
    },

    getEventCategories() {
      let params = {
        page: 1,
        limit: 10000
      };
      EVENTS_CATEGORY(params).then(res => {
        this.categoryOptions = res.message.data;
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    closeVendorCompany(i) {
      let index = this.showVendorCompanyData.indexOf(i);

      if (index == -1) {
        this.showVendorCompanyData.push(i);
      } else {
        this.showVendorCompanyData.splice(index, 1);
      }
    },

    turnDealDetail(id) {
      this.$router.push({
        path: '/deals/detail',
        query: {
          id: id
        }
      });
    },

    eventPageSizeChange(e) {
      console.log(e);
    },

    eventPageChange(e) {
      this.jobPage = e;
      this.getMyJobs(e, this.eventLimit);
      console.log(e);
    },

    getMyEvents(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };

      if (this.categoryId) {
        params.category_id = this.categoryId;
      }

      EVENTS_MY_EVENT(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.eventsList = res.message.data; // console.log(res.message.data)

          this.eventTotalNum = res.message.total;
        } else {
          console.log(res.msg);
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    turnDetail(id, t) {
      this.$router.push({
        path: '/events/detail',
        query: {
          id: id,
          t: t
        }
      });
    },

    turnPostEvent() {
      this.$router.push({
        path: '/events/post',
        query: {}
      });
    }

  }
};